@import 'helpers/variables';
@import 'helpers/mixins';
@import 'helpers/functions';
@import 'vendor/libs';
@import 'base/fonts';
@import 'base/general';
@import '../blocks/modules';

.promo {
  display: flex;
  margin: 0 0 63px;

  &__heading-wrapper {
    background-color: $pink;
    flex-grow: 1;
    align-items: center;
    display: flex;
    padding: 0 0 0 92px;
  }

  &__heading {
    font-size: 48px;
    font-weight: 900;
    line-height: 58px;
    text-transform: uppercase;
    position: relative;
    &:before {
      content: '';
      position: absolute;
      width: 12px;
      height: 92px;
      background-color: $dark-pink;
      right: 104%;
      top: 4%;
    }
  }

  &__sub-heading {
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: 38px;
    display: block;
  }

  &__sub-heading-pink {
    color: $dark-pink;
  }
}

.categories {
  &__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__item {
    width: 360px;
    min-height: 260px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  &__text {
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    color: #ffffff;
    text-transform: uppercase;
    margin: 0 0 6px 0;
  }

  &__heading {
    font-size: 24px;
    line-height: 19px;
    color: $dark-pink;
    text-transform: uppercase;
  }

  &__item-bg {
    background-color: rgba(33, 22, 22, 0.7);
  }

  &__item-woman {
    background: #fff url('../img/categories-woman.jpg') no-repeat;
    background-size: cover;
    margin: 0 30px 0 0;
    box-shadow: 0 0 0 130px rgba(33, 22, 22, 0.7) inset;
  }

  &__item-man {
    background: #fff url('../img/categories-man.jpg') no-repeat;
    background-size: cover;
    margin: 0 30px 0 0;
    box-shadow: 0 0 0 130px rgba(33, 22, 22, 0.7) inset;
  }

  &__item-kids {
    background: #fff url('../img/categories-kids.jpg') no-repeat;
    background-size: cover;
    box-shadow: 0 0 0 130px rgba(33, 22, 22, 0.7) inset;
  }

  &__item-accessories {
    background: #fff url('../img/categories-accesories.jpg') no-repeat;
    background-size: cover;
    flex-grow: 1;
    margin: 30px 0 97px 0;
    box-shadow: 0 0 0 130px rgba(33, 22, 22, 0.7) inset;
  }

  &__item-woman:hover,
  &__item-man:hover,
  &__item-kids:hover,
  &__item-accessories:hover {
    box-shadow: none;
  }
}

.products {
  &__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__heading {
    font-size: 30px;
    line-height: 36px;
    text-align: center;
  }

  &__text {
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    color: $grey;
    margin: 6px 0 48px 0;
  }

  &__item {
    width: 360px;
    margin: 0 0 48px 0;
  }

  &__item-woman {
    text-align: center;
    background-color: #f7e4e2;
    flex-grow: 1;
  }

  &__item-heading {
    text-transform: uppercase;
    font-size: 13px;
    line-height: 16px;
    margin: 25px 0 12px 16px;
  }

  &__item-text {
    font-weight: 300;
    font-size: 14px;
    line-height: 17px;
    color: #5d5d5d;
    margin: 0 29px 18px 16px;
  }

  &__item-price {
    color: $dark-pink;
    font-size: 16px;
    line-height: 19px;
    margin: 0 0 20px 18px;
  }

  &__item-button {
    display: none;
  }

  &__button {
    display: flex;
    margin: 0 auto 95px;
    padding: 14px 38px 13px;
    color: $dark-pink;
    border: 1px solid;
    background-color: transparent;
    &:hover {
      background-color: $dark-pink;
      color: #fff;
    }
  }
}

.quality {
  background-color: $dark-blue;

  &__list {
    display: flex;
    justify-content: space-between;
    text-align: center;
    padding: 103px 0;
  }

  &__item-img {
    margin: 0 0 28px;
  }

  &__item-heading {
    margin: 0 0 16px;
    font-size: 20px;
    line-height: 24px;
    font-weight: normal;
    color: #fbfbfb;
  }

  &__item-text {
    font-weight: 300;
    font-size: 14px;
    line-height: 17px;
    color: #fbfbfb;
    opacity: 0.7;
  }
}

.registration {
  &__heading {
    margin: 59px 0 0 0;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 29px;
    color: $dark-pink;
    text-transform: uppercase;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    margin: 124px 0 0 0;
  }

  &__form {
    margin: 0 128px 0 0;
  }

  &__form-radio {
    font-weight: 300;
    font-size: 11px;
    line-height: 13px;
  }

  &__form-text {
    margin: 15px 0 39px 0;
    width: 360px;
    font-style: normal;
    font-weight: 300;
    font-size: 13px;
    line-height: 16px;
    color: #b1b1b1;
  }

  &__form-button {
    padding: 16px 0 17px 29px;
    text-transform: uppercase;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #ffffff;
    background-color: $dark-pink;
    border: none;
    margin: 0 0 96px 0;
  }

  &__form-img {
    margin: 0 29px 0 20px;
  }
}

.input__form {
  &__form-male {
    margin: 31px 10px 31px 0;
    vertical-align: sub;
  }

  &__form-female {
    margin: 31px 10px 0 20px;
    vertical-align: sub;
  }
}

.loyalty {
  &__heading {
    text-transform: uppercase;
  }

  &__heading,
  &__text,
  &__item {
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
  }

  &__text {
    margin: 22px 0 21px 0;
  }

  &__item {
    margin: 0 0 16px 43px;
    position: relative;
    &:before {
      content: '';
      width: 20px;
      height: 16px;
      background-image: url('../img/svg/ok.svg');
      position: absolute;
      top: 7px;
      left: -43px;
    }
  }
}

.cart-heading {
  background: #f8f3f4;
  margin: 0 0 96px 0;
  &__title {
    padding: 60px 0;
    text-transform: uppercase;
    color: $dark-pink;
    font-weight: normal;
    font-size: 24px;
    line-height: 29px;
  }
}

.cart {
  display: flex;
  &__list-block {
    display: flex;
    width: 652px;
    box-shadow: 24px 23px 27px #e1e1e1;
    margin: 0 0 46px 0;
  }
  &__form {
    margin: 0 0 0 129px;
    legend {
      font-weight: 300;
      font-size: 16px;
      line-height: 19px;
      text-transform: uppercase;
    }
  }
}
